<template>
  <div id="ListCo">
    <!-- <ItemCo/> -->

    <!-- 放一個initial_background解決list長度不足時有空白的Bug，不限制list高度讓內容太多時自動延長背景，這裡有點傻瓜式，猜測有更優美寫法 -->
    <div id="initial_background"></div>
    <div id="list">
      <!-- <input type="text" ref="test_ref"> -->
      <ul>
        <!-- 遍歷list，展示出來 -->
        <li v-for="(i, index) of list_filtered" :key="i.id">
            <img src="../assets/handle.png" class="handle" alt="手把圖案">
            <!-- 修改樣式的checkbox -->
            <label class="container checkbox">
              <input type="checkbox" v-model="i.done">
              <span class="checkmark"></span>
            </label>
            <input type="text" v-model="i.text_input" :id="index" :alt="index" @keyup.enter="add_item" class="text_input" autocomplete="off">
            <img src="../assets/delete_icon.png" @click="delete_item" class="delete_icon"  :alt="index">
        </li>
      </ul>
      

      <!-- <p>----下面為測試超出頁面內容---</p>
      <span v-for="(i,index) in 50" :key="index">
        666
      </span>
      <ul>
        <li v-for="(i,index) in 50" :key="index">
          {{i}}
        </li>
      </ul> -->

      <!-- 放一個墊底避免底下內容被BottomArea擋住，這裡有點傻瓜式，猜測有更優美寫法 -->
      <div id="bottom_stupid"></div>
    </div>

    <!-- 切換清單按鈕，應該坐在BottomArea裡面，等學會組件間傳輸之後 -->
    <div id="button_done_or_not" @click="button_done_or_not">
      切換清單
    </div>
  </div>
</template>

<script>
    // import ItemCo from './components/ItemCo.vue'
    // 引入nanoid，生成隨機唯一id
    import {nanoid} from "nanoid"

    export default {
      name: 'ListCo',
      components: {
        // ItemCo,
      },
      data() {
        return {
            // 從本地儲存中獲取數據，null就創建初始化數組
            list:JSON.parse(localStorage.getItem("list")) || [{id:nanoid(), done:false, text_input:'輸入完成後按下Enter'}],
            focus_index: "",
            new_item_or_not: false,
            // change_list = false 代表正曬出代辦事項
            change_list: false,
        }
      },
      methods: {
        // 新增item
        add_item(text_input) {
          if (this.change_list === true){
            console.log("正曬初完成事項，輸入enter新增完成的item")
          }

          console.log("放開Enter");
          // console.log(Number(text_input.target.alt) + 1);
          var new_item = {
            // 使用nanoid函數生成隨機唯一id，如果需要更好的可以使用uuid
            id:nanoid(),
            // 根據目前是在展示done or not done決定新的idem.done是treu or false
            done:this.change_list,
            text_input:''
          };
          // 在當前index位置的+1插入new_item，當前index位置藉由alt傳送
          var new_item_index = (Number(text_input.target.alt) + 1);
          this.list.splice(new_item_index, 0, new_item);
          // focus在新生成的item上
          // this.$refs.("1").focus();
          console.log(new_item_index);
          // document.getElementById(new_item_index).focus();
          // 更新focus資訊給updated鉤子使用
          this.focus_index = new_item_index;
          this.new_item_or_not = true;
        },
        // 刪除item
        delete_item(event) {
          // 這裡可能有更優雅的寫法，而不是把index再次寫入alt
          console.log("點擊刪除"+event.target.alt);
          // Bug：刪除完成事項可能會刪成未完成事項
          if (this.list.length > 1) {
            this.list.splice(event.target.alt,1);
          }         
        },
        // 切換清單
        button_done_or_not() {
          this.change_list = ! this.change_list;
        },
      },

      // 生命週期函數(生命週期鉤子)，初始掛載完後執行(虛擬DOM轉換成真實DOM之後)
      mounted() {
        // 載入頁面後fucus在第一個item
        document.getElementById(0).focus();
      },

      // 生命週期函數，數據更新完成後執行(虛擬DOM轉換成真實DOM之後)
      updated() {
        // fucus在新的item
        if (this.new_item_or_not) {
          document.getElementById(this.focus_index).focus();
          this.new_item_or_not = false
        }
        
      },
      // 監視list
      watch: {
          list:{
              // 使用深度監視，監視list裡面所有子項目的變動
              deep:true,
              handler(value){
                  // list發生改變就存到本地
                  // JSON轉化為字符串才不會存成[object Object]
                  localStorage.setItem('list', JSON.stringify(value));

                  // 計算未完成的事情數量，利用過濾函數filter
                  var un_done_number = this.list.filter((i) => {
                  return i.done === false
                  });
                  console.log("未完成事情數量" + un_done_number.length);
                  if (un_done_number.length === 0) {
                      // list中無done=false時自動添加，避免無item Bug
                      var new_item = {
                        // 使用nanoid函數生成隨機唯一id，如果需要更好的可以使用uuid
                        id:nanoid(),
                        done:false,
                        text_input:''
                      };
                      // 在index=0插入new_item
                      this.list.splice(0 , 0, new_item);
                      console.log("歸零啦！自動新增！")
                      // 更新focus資訊給updated鉤子使用
                      this.focus_index = 0;
                      this.new_item_or_not = true;

                  }
              },


          },
      },
      // 列表過濾，使用關鍵字模糊搜索，這個代碼有bug？
      // computed: {
      //   list_filtered() {
      //     return this.list.filter((l) => {
      //       return l.done.indexof("b") !== -1
      //     })
      //   }
      // }
      computed: {
        list_filtered() {
          // 過濾清單，是否done，利用過濾函數filter
          return this.list.filter((i) => {
            return i.done === this.change_list
          })
        },
      },
    }
</script>

<style scoped>
    #initial_background, #list {
      position: absolute;
      top: 10vh;
      left: 0px;

      width: 100vw;

      background-color: rgb(40, 40, 40);
    }

    #initial_background {
      height: 80vh;    
    }

    #list {        
      color: rgb(197, 197, 197);

      /* font-size: xx-large; */

      /* 文字自動換行，也許用不到？ */
      word-wrap: break-word;
    }

    #bottom_stupid {
      height: 10vh;
    }

    ul {
      /* 取消列表符號 */
      list-style-type: none;
      /* 讓li左移，左右邊padding調整相同 */
      padding:0 2vw;
    }

    /* 用font-size控制長寬 */
    .handle, .checkbox, .text_input, .delete_icon {
      position: relative;

      font-size: larger;
    }

    .handle {
      top: 0.55em;

      width: 2.1em;
      height: 2.1em;
    }

    .delete_icon {
      top: 0.15em;
      margin-left: 0.3em;

      width: 1em;
      height: 1em;
    }

    .checkbox {
      top: -0.05em;
      margin-right: 0em;
      margin-left: 0.1em;

      /* 這裡有Bug，大小要進去底下控制，無法隨字體自動調整大小，用別人的東西果然要付出代價... */
      /* width: 3em;
      height: 3em; */
    }

    /* 修改checkbox樣式 */
    /* The container */
    .container {
      /* 這裡是我註釋掉的，避免變成區塊元素 */
      /* display: block; */
      position: relative;
      padding-left: 35px;
      margin-bottom: 12px;
      cursor: pointer;
      font-size: 22px;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
    }

    /* Hide the browser's default checkbox */
    .container input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;
    }

    /* Create a custom checkbox */
    .checkmark {
      position: absolute;
      top: 0;
      left: 0;
      height: 25px;
      width: 25px;
      /* 我修改成黑色樣式 */
      background-color: rgba(255, 255, 255, 0);
      border:solid 1.5px white;
    }

    /* 我註釋掉，避免手機板點擊取消後會殘留顏色Bug */
    /* On mouse-over, add a grey background color */
    /* .container:hover input ~ .checkmark {
      background-color: #ccc;
    } */

    /* When the checkbox is checked, add a blue background */
    .container input:checked ~ .checkmark {
      /* 換成透明背景 */
      background-color: #ffffff00;
    }

    /* Create the checkmark/indicator (hidden when not checked) */
    .checkmark:after {
      content: "";
      position: absolute;
      display: none;
    }

    /* Show the checkmark when checked */
    .container input:checked ~ .checkmark:after {
      display: block;
    }

    /* Style the checkmark/indicator */
    .container .checkmark:after {
      left: 9px;
      top: 5px;
      width: 5px;
      height: 10px;
      border: solid white;
      border-width: 0 3px 3px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
    }

    .text_input {
      /* 這裡有bug，text_input右邊界會因為字體大小而在不同裝置不一樣 */
      width: 65vw;

      /* 背景透明 */
      background-color: rgba(255, 255, 255, 0);
      border:none;

      color: rgb(205, 205, 205);
    }

    #button_done_or_not {
      position: fixed;
      bottom: 12vh;
      right: 5vw;
      text-align: center;

      width: 25vw;
      line-height: 10vh;
      height: 10vh;
      font-size: x-large;

      border: solid 1.5px white;
      background-color: rgb(70, 70, 70);
      color: white;

    }
</style>
